<template>
  <external-report-viewer
    v-if="dashboardType === 'LOOKER'"
    :url="lookerUrl"
    class="looker_report"
  />
  <section v-else-if="dashboardType === 'DEFAULT'" class="dashboard_section">
    <div
      v-if="selectedStoreAddresses.length > 1"
      class="dashboard_section_info"
    >
      {{ $t('dashboard.results') }}
    </div>
    <div class="dashboard_section_charts_section">
      <div class="dashboard_section_chart_and_title">
        <div class="dashboard_section_title">
          {{ $t('dashboard.orders.title') }}
        </div>
        <div class="dashboard_section_chart_section">
          <div class="dashboard_section_chart_section_chart">
            <bar-chart
              key="totalOrders"
              :width="345"
              :height="260"
              :options="barChartOptions"
              :chart-data="totalOrders"
            ></bar-chart>
          </div>
          <div class="dashboard_section_chart_section_numbers_number">
            <span
              class="dashboard_section_chart_section_numbers_round"
              :class="{
                dashboard_section_chart_section_numbers_round_small: tooLong(
                  substractDataFromDataset(totalOrders)
                )
              }"
            >
              {{ substractDataFromDataset(totalOrders) }}
            </span>
            <div class="dashboard_section_chart_section_numbers_title">
              {{ $t('dashboard.orders.reports.week.hint') }}
            </div>
          </div>
        </div>
      </div>
      <div class="dashboard_section_chart_and_title">
        <div class="dashboard_section_title">
          {{ $t('dashboard.orders.reports.revenues.hint') }}
        </div>
        <div class="dashboard_section_chart_section">
          <div class="dashboard_section_chart_section_chart">
            <bar-chart
              key="revenue"
              :width="345"
              :height="260"
              :options="barChartOptions"
              :chart-data="revenue"
            >
            </bar-chart>
          </div>
          <div class="dashboard_section_chart_section_numbers_number">
            <span
              class="dashboard_section_chart_section_numbers_round"
              :class="{
                dashboard_section_chart_section_numbers_round_small: tooLong(
                  substractDataFromDataset(revenue)
                )
              }"
            >
              {{ substractDataFromDataset(revenue) }}
            </span>
            <div class="dashboard_section_chart_section_numbers_title">
              {{ $t('dashboard.orders.reports.week.hint') }}
            </div>
          </div>
        </div>
      </div>
      <div class="dashboard_section_chart_and_title">
        <div class="dashboard_section_title">
          {{ $t('dashboard.orders.reports.cancel.title') }}
        </div>
        <div class="dashboard_section_chart_section">
          <div class="dashboard_section_chart_section_chart">
            <line-chart
              key="cancelOrders"
              :width="345"
              :height="260"
              :options="lineChartOptions"
              :chart-data="cancelOrders"
            >
            </line-chart>
          </div>
          <div class="dashboard_section_chart_section_numbers_number">
            <span
              class="dashboard_section_chart_section_numbers_round"
              :class="{
                dashboard_section_chart_section_numbers_round_small: tooLong(
                  substractDataFromDataset(cancelOrders)
                )
              }"
            >
              {{ `${substractDataFromDataset(cancelOrders)}%` }}
            </span>
            <div class="dashboard_section_chart_section_numbers_title">
              {{ $t('dashboard.orders.reports.week.hint') }}
            </div>
          </div>
        </div>
      </div>
      <div class="dashboard_section_chart_and_title">
        <div class="dashboard_section_title">
          {{ $t('dashboard.orders.reports.badratings.title') }}
        </div>
        <div class="dashboard_section_chart_section">
          <div class="dashboard_section_chart_section_chart">
            <line-chart
              key="badReviews"
              :width="345"
              :height="260"
              :options="lineChartOptions"
              :chart-data="badReviews"
            >
            </line-chart>
          </div>
          <div class="dashboard_section_chart_section_numbers_number">
            <span
              class="dashboard_section_chart_section_numbers_round"
              :class="{
                dashboard_section_chart_section_numbers_round_small: tooLong(
                  substractDataFromDataset(badReviews)
                )
              }"
            >
              {{ substractDataFromDataset(badReviews) }}
            </span>
            <div class="dashboard_section_chart_section_numbers_title">
              {{ $t('dashboard.orders.reports.week.hint') }}
            </div>
          </div>
        </div>
      </div>
      <div
        v-if="timeToAcceptEnabled"
        class="dashboard_section_chart_and_title"
        data-qa="time-to-accept-section"
      >
        <div class="dashboard_section_title">
          {{ $t('dashboard.orders.reports.timeToAccept.title') }}
        </div>
        <div class="dashboard_section_chart_section">
          <div class="dashboard_section_chart_section_chart">
            <line-chart
              key="timeToAccept"
              :width="345"
              :height="260"
              :options="lineChartTimeOptions"
              :chart-data="timeToAccept"
            >
            </line-chart>
          </div>
          <div class="dashboard_section_chart_section_numbers">
            <div class="dashboard_section_chart_section_numbers_number">
              <span
                class="dashboard_section_chart_section_numbers_round"
                :class="{
                  dashboard_section_chart_section_numbers_round_small: tooLong(
                    substractDataFromDataset(timeToAccept)
                  )
                }"
              >
                {{ substractDataFromDataset(timeToAccept) }}
              </span>
              <div class="dashboard_section_chart_section_numbers_title">
                {{ $t('dashboard.orders.reports.week.hint') }}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="dashboard_section_chart_and_title">
        <div class="dashboard_section_title">
          {{ $t('dashboard.orders.reports.bestSellers.hint') }}
        </div>
        <div class="dashboard_section_chart_section">
          <div class="dashboard_section_chart_section_chart">
            <horizontal-chart
              key="bestSellers"
              :width="345"
              :height="260"
              :options="chartOptionsHorizontal"
              :chart-data="convertBestSellers"
            >
            </horizontal-chart>
          </div>
        </div>
      </div>
    </div>
  </section>
  <div v-else>
    {{ $t('sa.stats.loading') }}
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DISPLAY_REPORTS_DASHBOARD } from '@/modules/store/services/display-types'
import ExternalReportViewer from './ExternalReportViewer'
import BarChart from '@/components/common/charts/BarChart'
import LineChart from '@/components/common/charts/LineChart'
import HorizontalChart from '@/components/common/charts/HorizontalChart'
import StatsHelper from './statsHelper'
import {
  GET_CURRENT_STORE_ADMIN_GETTER,
  GET_SELECTED_STORE_ADDRESS_IDS_GETTER
} from '@/modules/authentication'
import { GET_TOGGLES_GETTER } from '../../../../modules/feature-toggles/store'
import { ONE_SECOND_IN_MILLIS } from '@/modules/core/helpers'

export default {
  name: 'Dashboard',
  components: {
    ExternalReportViewer,
    BarChart,
    LineChart,
    HorizontalChart
  },
  data() {
    return {
      barChartOptions: {
        responsive: false,
        legend: {
          display: false
        },
        scales: {
          xAxes: [
            {
              categoryPercentage: 0.9,
              barPercentage: 0.9,
              barThickness: 20,
              ticks: {
                autoSkip: false,
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ],
          yAxes: [
            {
              ticks: {
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ]
        },
        tooltips: {
          callbacks: {
            label: function (tooltipItem) {
              return tooltipItem.yLabel
            }
          }
        }
      },
      lineChartOptions: {
        responsive: false,
        legend: {
          display: false
        },
        scales: {
          xAxes: [
            {
              categoryPercentage: 0.9,
              barPercentage: 0.9,
              ticks: {
                autoSkip: false,
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ],
          yAxes: [
            {
              ticks: {
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ]
        },
        tooltips: {
          callbacks: {
            label: function (tooltipItem) {
              return `${tooltipItem.yLabel} %`
            }
          }
        }
      },
      lineChartTimeOptions: {
        responsive: false,
        legend: {
          display: false
        },
        scales: {
          xAxes: [
            {
              categoryPercentage: 0.9,
              barPercentage: 0.9,
              ticks: {
                autoSkip: false,
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ],
          yAxes: [
            {
              ticks: {
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ]
        },
        tooltips: {
          callbacks: {
            label: function (tooltipItem) {
              return `${tooltipItem.yLabel} min`
            }
          }
        }
      },
      chartOptionsHorizontal: {
        scales: {
          yAxes: [
            {
              ticks: {
                callback: function (value) {
                  if (value.length > 26) {
                    return `${value.substr(0, 23)}...`
                  } else {
                    return value
                  }
                },
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                fontColor: '#3e3e3e',
                fontFamily: 'Gotham-Medium'
              }
            }
          ]
        },
        legend: {
          display: true,
          labels: {
            fontColor: '#3e3e3e',
            fontFamily: 'Gotham-Medium'
          }
        },
        tooltips: {
          callbacks: {
            label: function (tooltipItem) {
              return tooltipItem.xLabel
            }
          }
        }
      }
    }
  },
  computed: {
    ...mapGetters(['dashboardType', 'lookerUrl', 'statistics', 'bestSellers']),
    ...mapGetters('authentication', [
      GET_CURRENT_STORE_ADMIN_GETTER,
      GET_SELECTED_STORE_ADDRESS_IDS_GETTER
    ]),
    ...mapGetters({
      toggles: GET_TOGGLES_GETTER
    }),
    user() {
      return this[GET_CURRENT_STORE_ADMIN_GETTER]
    },
    selectedStoreAddresses() {
      return this[GET_SELECTED_STORE_ADDRESS_IDS_GETTER]
    },
    timeToAcceptEnabled() {
      return this.toggles.FE_PARTNERS_TIME_TO_ACCEPT_ENABLED
    },
    totalOrders() {
      return StatsHelper.toDataCollection(
        this.statistics.totalOrders,
        this.statistics.weeks,
        null
      )
    },
    revenue() {
      return StatsHelper.toDataCollection(
        this.statistics.revenue,
        this.statistics.weeks,
        null
      )
    },
    cancelOrders() {
      return StatsHelper.toDataCollection(
        this.statistics.cancelOrders,
        this.statistics.weeks,
        null
      )
    },
    badReviews() {
      return StatsHelper.toDataCollection(
        this.statistics.badReviews,
        this.statistics.weeks,
        null
      )
    },
    timeToAccept() {
      return StatsHelper.toDataCollection(
        this.statistics.timeToAccept,
        this.statistics.weeks,
        'TIME_TO_ACCEPT'
      )
    },
    convertBestSellers() {
      const locales = {
        currentMonth: this.$i18n.t('dashboard.orders.reports.month.current'),
        lastMonth: this.$i18n.t('dashboard.orders.reports.month.last')
      }
      return StatsHelper.convertBestSellers(this.bestSellers, locales)
    },
    userHasStoreAddress() {
      return (
        this.user &&
        this.user.store.storeAddresses &&
        this.user.store.storeAddresses.length > 0
      )
    }
  },
  watch: {
    user() {
      if (this.userHasStoreAddress) {
        this.getLookerUrl()
      }
    },
    selectedStoreAddresses() {
      this.getStatistics()
      this.getBestSellers()
    }
  },
  activated() {
    this.displayState(DISPLAY_REPORTS_DASHBOARD)
  },

  mounted() {
    this.displayState(DISPLAY_REPORTS_DASHBOARD)
    this.getLookerUrl()
    setTimeout(() => {
      if (this.statistics.weeks.length === 0) {
        this.getStatistics()
        this.getBestSellers()
      }
    }, ONE_SECOND_IN_MILLIS)
  },
  methods: {
    ...mapActions([
      'getLookerUrl',
      'getStatistics',
      'getBestSellers',
      'displayState'
    ]),
    tooLong(data) {
      if (!data) {
        return false
      }
      return `${data}`.length > 4
    },
    substractDataFromDataset(field) {
      if (field.datasets[0]) {
        return field.datasets[0].data[field.datasets[0].data.length - 1]
      }
    }
  }
}
</script>

