import moment from 'moment-timezone'

const emptyData = {
  labels: [],
  datasets: []
}

const toDataCollection = function (data, labels, type) {
  if (!data || !labels) {
    return emptyData
  }
  if (type) {
    data = data.map(secToMin)
  }
  data = data.map(round2dec)
  labels = labels.map(getMonthName)
  const dataCollection = {
    labels,
    datasets: [
      {
        data,
        backgroundColor: '#d8d8d8',
        borderColor: '#d8d8d8',
        borderWidth: 1
      }
    ]
  }
  return dataCollection
}

function round2dec(num) {
  return Math.round(num * 100) / 100
}

function secToMin(secs) {
  return Math.floor(secs / 60)
}

function getMonthName(date) {
  const m = date.split('/')
  return `${moment(m[0], 'MM').format('MMMM')} ${m[1]}`
}

const convertBestSellers = function (bestSellers, locales) {
  if (!bestSellers.lastMonth || !bestSellers.currentMonth) {
    return emptyData
  }
  const labels = bestSellers.lastMonth.slice(0, 9).map(getProductsNames)
  const dataLastMonth = bestSellers.currentMonth
    .slice(0, 9)
    .map(getProductsQuantity)
  const dataCurrentMonth = bestSellers.lastMonth
    .slice(0, 9)
    .map(getProductsQuantity)
  const dataCollection = {
    labels: labels,
    datasets: [
      {
        label: locales.currentMonth,
        data: dataCurrentMonth,
        backgroundColor: '#ffc244',
        borderColor: '#ffc244',
        borderWidth: 1
      },
      {
        label: locales.lastMonth,
        data: dataLastMonth,
        backgroundColor: '#d8d8d8',
        borderColor: '#d8d8d8',
        borderWidth: 1
      }
    ]
  }
  return dataCollection
}

function getProductsNames(product) {
  return product.name
}

function getProductsQuantity(product) {
  return product.quantity
}

export default {
  toDataCollection,
  convertBestSellers
}

export const _toDataCollection = toDataCollection
export const _convertBestSellers = convertBestSellers
